import { createRouter, createWebHashHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'
import { LOGIN_COOKIE_TOKEN, SYSTEM_CONFIG } from '@/constants/global'
import { ROUTER_PATH } from '@/constants/menu'
// import ManagementLayout from '../layouts/ManagementLayout.vue'
import BasicLayout from '../layouts/BasicLayout.vue'
import { getCookie } from '@/utils/cookie'
import type { RouteLocationNormalized } from 'vue-router'

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    component: BasicLayout,
    children: [
      {
        path: ROUTER_PATH.patientInfo,
        name: 'PatientInfo',
        component: () => import('../views/patient/PatientInfo.vue'),
        meta: { title: '患者管理' },
      },
      {
        path: ROUTER_PATH.patientDetail + '/:id/:name',
        name: 'PatientDetail',
        component: () => import('../views/patient/patientDetail/PatientDetail.vue'),
        meta: {
          title: (route: RouteLocationNormalized) => `病患详情：${route.params.name || '未知患者'}`,
          // title: '病患详情',
          dynamicTitle: true,
          // 添加 key 属性用于强制组件重新渲染
          key: (route: RouteLocationNormalized) => route.fullPath,
          activeMenu: ROUTER_PATH.patientInfo, // 指定要高亮的菜单路径
        },
      },
      {
        path: ROUTER_PATH.patientRecords,
        name: 'PatientRecords',
        component: () => import('../views/patient/PatientRecords.vue'),
        meta: {
          title: '病历管理',
          activeMenu: ROUTER_PATH.patientInfo, // 病历管理也高亮患者管理
        },
      },
      {
        path: ROUTER_PATH.patientAppointments,
        name: 'PatientAppointments',
        component: () => import('../views/patient/PatientAppointments.vue'),
        meta: {
          title: '预约管理',
          activeMenu: ROUTER_PATH.patientInfo, // 预约管理也高亮患者管理
        },
      },
      // 工勤管理
      {
        path: ROUTER_PATH.logistics,
        name: 'LogisticsDashboard',
        component: () => import('../views/logistics/LogisticsDashboard.vue'),
        meta: { title: '工勤管理' },
      },
      // 敏感指标
      {
        path: ROUTER_PATH.indicators,
        name: 'IndicatorsDashboard',
        component: () => import('../views/indicators/IndicatorsDashboard.vue'),
        meta: { title: '敏感指标' },
      },
      // 统计分析
      {
        path: ROUTER_PATH.statistics,
        name: 'StatisticsDashboard',
        component: () => import('../views/statistics/StatisticsDashboard.vue'),
        meta: { title: '统计分析' },
      },
      {
        path: ROUTER_PATH.systemConfig,
        name: 'SystemConfig',
        component: () => import('../views/system/SystemConfig.vue'),
        meta: { title: '系统设置' },
      },
      {
        path: ROUTER_PATH.welcome,
        name: 'Welcome',
        component: () => import('../views/HomeView.vue'),
        meta: { title: '首页', sticky: true },
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/login/Login.vue'),
    meta: { title: '登录' },
  },
  // {
  //   path: '/:pathMatch(.*)*',
  //   redirect: '/welcome',
  // },
]
const router = createRouter({
  history: createWebHashHistory(import.meta.env.VITE_ROUTE_BASE), // 路由基础路径
  routes,
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  // 设置页面标题
  const title = typeof to.meta.title === 'function' ? to.meta.title(to) : (to.meta.title as string)
  if (title) {
    document.title = `${title}${SYSTEM_CONFIG.TITLE_SEPARATOR}${SYSTEM_CONFIG.TITLE}`
  } else {
    document.title = SYSTEM_CONFIG.TITLE
  }

  // 获取cookie中的token
  const token = getCookie(LOGIN_COOKIE_TOKEN)

  // 如果访问的不是登录页且没有token,则跳转到登录页
  if (to.path !== '/login' && !token) {
    next('/login')
    return
  }

  // 如果访问登录页且有token,则跳转到首页
  if (to.path === '/login' && token) {
    next('/')
    return
  }

  next()
})

export default router
