import { MockMethod } from 'vite-plugin-mock'

// 手术类型枚举
export enum SurgeryType {
  ELECTIVE = 'elective', // 择期
  EMERGENCY = 'emergency', // 急诊
  DAY = 'day', // 日间
}

// 生成随机患者数据
const generatePatients = (count: number) => {
  const patients: any[] = []
  for (let i = 0; i < count; i++) {
    patients.push({
      id: `ZY${String(Math.floor(Math.random() * 100000)).padStart(6, '0')}`, // 住院号
      name: ['张三', '李四', '王五', '赵六', '钱七'][Math.floor(Math.random() * 5)],
      gender: Math.random() > 0.5 ? '男' : '女',
      age: Math.floor(Math.random() * 50) + 20,
      bloodType: ['A型血', 'B型血', 'O型血', 'AB型血'][Math.floor(Math.random() * 4)],
      ward: ['外科', '骨科', '神经外科', '心胸外科'][Math.floor(Math.random() * 4)], // 病区
      bedNumber: `${Math.floor(Math.random() * 20) + 1}床`,
      medicalRecordNumber: `BA${String(Math.floor(Math.random() * 100000)).padStart(6, '0')}`, // 病案号
      operatingRoom: ['04', '13', '14', '15'][Math.floor(Math.random() * 4)] + '手术间',
      sequence: Math.floor(Math.random() * 10) + 1, // 台次
      surgeryTime: new Date(Date.now() + Math.random() * 7 * 24 * 60 * 60 * 1000).toISOString(),
      surgeryName: ['腹腔镜胆囊切除术腹腔镜胆囊切除术', '阑尾切除术', '疝气修补术', '骨折内固定术'][
        Math.floor(Math.random() * 4)
      ],
      nursingLevel: ['特级护理', '一级护理', '二级护理'][Math.floor(Math.random() * 3)],
      allergies: Math.random() > 0.7 ? ['青霉素', '磺胺', '碘'][Math.floor(Math.random() * 3)] : '', // 过敏史
      insuranceType: ['城镇职工', '城镇居民', '新农合', '自费'][Math.floor(Math.random() * 4)], // 医保类型
      surgeryType: Object.values(SurgeryType)[Math.floor(Math.random() * 3)], // 手术类型
      surgeryStatus: ['pending', 'in_progress', 'completed'][Math.floor(Math.random() * 3)],
      surgeonName: ['张医生', '李医生', '王医生'][Math.floor(Math.random() * 3)], // 手术医生
      anesthesiologistName: ['刘医生', '陈医生'][Math.floor(Math.random() * 2)], // 麻醉医生
      circuitNurse: ['赵护士', '钱护士', '孙护士'][Math.floor(Math.random() * 3)], // 巡回护士
      anesthesiaNumber: `MA${String(Math.floor(Math.random() * 10000)).padStart(4, '0')}`, // 手麻编号
      anesthesiaMethod: ['全麻', '局麻', '硬膜外麻醉'][Math.floor(Math.random() * 3)], // 麻醉方式
      // 病人标识
      flags: {
        isAllergic: Math.random() > 0.7, // 过敏标识
        hasVTE: Math.random() > 0.8, // VTE标识
        hivStatus: Math.random() > 0.9 ? 'positive' : 'negative', // HIV状态
        hasArrearage: Math.random() > 0.9, // 欠费标识
        syphilisStatus: Math.random() > 0.9 ? 'positive' : 'negative', // 梅毒状态
      },
    })
  }
  return patients
}

export default [
  {
    url: '/api/patient/list',
    method: 'post',
    response: () => {
      return {
        code: 200,
        success: true,
        data: {
          total: 20,
          list: generatePatients(20),
        },
      }
    },
  },
] as MockMethod[]
