import { defineStore } from 'pinia'
import { ref, watch } from 'vue'
import { LOCAL_STORAGE_KEY } from '@/constants/global'

export interface TabItem {
  path: string
  title: string
  name: string
  closable?: boolean
  [key: string]: any
}

export const useTabsStore = defineStore('tabs', () => {
  // 从 localStorage 加载数据
  const loadFromStorage = () => {
    try {
      const storedTabs = localStorage.getItem(LOCAL_STORAGE_KEY.tabs)
      const storedActiveTab = localStorage.getItem(LOCAL_STORAGE_KEY.activeTab)

      if (storedTabs) {
        tabs.value = JSON.parse(storedTabs)
        // 重新构建缓存视图列表
        cachedViews.value = tabs.value.filter((tab) => tab.name).map((tab) => tab.name)
      }

      if (storedActiveTab) {
        activeTab.value = storedActiveTab
      }
    } catch (error) {
      console.error('Failed to load tabs from localStorage:', error)
    }
  }

  // 保存数据到 localStorage
  const saveToStorage = () => {
    try {
      localStorage.setItem(LOCAL_STORAGE_KEY.tabs, JSON.stringify(tabs.value))
      localStorage.setItem(LOCAL_STORAGE_KEY.activeTab, activeTab.value)
    } catch (error) {
      console.error('Failed to save tabs to localStorage:', error)
    }
  }

  // 清除存储的数据
  const clearStorage = () => {
    localStorage.removeItem(LOCAL_STORAGE_KEY.tabs)
    localStorage.removeItem(LOCAL_STORAGE_KEY.activeTab)
  }

  // 标签页列表
  const tabs = ref<TabItem[]>([])

  // 当前激活的标签页
  const activeTab = ref('')

  // 缓存的视图组件名称
  const cachedViews = ref<string[]>([])

  // 监听数据变化，自动保存到 localStorage
  watch(
    [tabs, activeTab],
    () => {
      saveToStorage()
    },
    { deep: true },
  )

  // 添加标签页
  const addTab = (tab: TabItem) => {
    const existingTab = tabs.value.find((t) => t.path === tab.path)
    if (existingTab) {
      // 如果标签页已存在且标题不同，更新标题
      if (existingTab.title !== tab.title) {
        existingTab.title = tab.title
      }
    } else {
      tabs.value.push(tab)
      // 只有当缓存中不存在时才添加
      if (tab.name && !cachedViews.value.includes(tab.name)) {
        cachedViews.value.push(tab.name)
      }
    }
    activeTab.value = tab.path
  }

  // 移除标签页
  const removeTab = (path: string) => {
    const index = tabs.value.findIndex((t) => t.path === path)
    if (index > -1) {
      const tab = tabs.value[index]
      tabs.value.splice(index, 1)

      // 从缓存中移除
      if (tab.name) {
        const cacheIndex = cachedViews.value.indexOf(tab.name)
        if (cacheIndex > -1) {
          cachedViews.value.splice(cacheIndex, 1)
        }
      }

      // 如果删除的是当前激活的标签页，切换到相邻的标签页
      if (activeTab.value === path && tabs.value.length > 0) {
        const newIndex = Math.max(0, index - 1)
        activeTab.value = tabs.value[newIndex].path
      }
    }
  }

  // 关闭其他标签页
  const closeOtherTabs = (path: string) => {
    const currentTab = tabs.value.find((t) => t.path === path)
    if (currentTab) {
      tabs.value = [currentTab]
      cachedViews.value = currentTab.name ? [currentTab.name] : []
      activeTab.value = path
    }
  }

  // 关闭所有标签页
  const closeAllTabs = () => {
    tabs.value = []
    cachedViews.value = []
    activeTab.value = ''
    clearStorage()
  }

  // 刷新标签页（移除缓存后重新添加）
  const refreshTab = (path: string) => {
    const tab = tabs.value.find((t) => t.path === path)
    if (tab && tab.name) {
      const cacheIndex = cachedViews.value.indexOf(tab.name)
      if (cacheIndex > -1) {
        cachedViews.value.splice(cacheIndex, 1)
        // 延迟重新添加到缓存，触发组件重新渲染
        setTimeout(() => {
          cachedViews.value.push(tab.name!)
        }, 100)
      }
    }
  }

  // 初始化时加载数据
  loadFromStorage()

  return {
    tabs,
    activeTab,
    cachedViews,
    addTab,
    removeTab,
    closeOtherTabs,
    closeAllTabs,
    refreshTab,
    clearStorage, // 导出清除方法供登出时使用
  }
})
